$h1-font-size: 1.8em;
$h2-font-size: 1.3em;
$h3-font-size: 1.3em;
$small-font-size: 1em;
$xsmall-font-size: 0.8em;
$color-text-black: #1d1d1b;

$color-blue: #273be2;
$color-pink: #f4c2c2;
$color-red: #fe6f5e;
$color-yellow: #f1b42f;

body {
    background: white;
    font-size: 1.1em;
    font-family: $font-cn-family;
    overflow-x: hidden;
    // font-family: MyCustomFont !important;

    h1 {
        font-size: $h1-font-size;
    }
    h2 {
        font-size: $h2-font-size;
    }
    h3 {
        font-size: $h3-font-size;
    }
    p {
        font-size: $base-font-size;
        font-weight: $base-font-weight;
    }

    .home {
        //min-height: 75vh;
    }
}

body:lang(en) {
    font-family: "Lato", sans-serif;
}

#bg-wrap {
    display: block;
    position: fixed;
    z-index: -100;
    width: 100vw;
    height: 100vh;
}

.home {
    min-height: 100vh;
    padding: 0 15px;
    // display: flex;
    // text-align: center;
    // align-items: center;
    // justify-content: center;
}

.site-header {
    // border-top: 5px solid $color-text-black;
    padding-top: 10px;

    //fix the header at the page top
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;

    //set the header background color as white with 0.9 color opacity241, 180, 47
    background: rgb(241, 180, 47);
    // background: rgba(255,255,255, 0.1);

    //background: #fff;
    // $color-yellow: #F1B42F;

    @include media-query($on-palm) {
        padding-top: 0;
    }
}

.site-nav {
    line-height: 50px;
    background: none;

    .menu-icon {
        padding-top: 13px;
    }

    &:hover {
        background: $color-yellow;
        box-shadow: none;

        .trigger {
            border: none;
            box-shadow: none;
        }
    }

    @include media-query($on-palm) {
        // position: absolute;

        width: 100%;
        // z-index: -1;
    }
}

.site-header .wrapper {
    padding: 5px 50px 10px;
}

.nav-page {
    display: float;
    text-align: right;
    margin: 40px 0 20px 10px;
    padding: 8px 20px;
    color: white !important;
    //font-size: 10px;
    font-weight: 300;
    //background-color: $color-text-black;
    border: 1px solid white;
    border-radius: 4px;
    font-size: 20px;
    font-weight: 500;

    &:hover {
        background-color: $color-red;
        // color: $color-text-black !important;
        text-decoration: none;
        border: 1px solid $color-red;
    }

    @include media-query($on-palm) {
        display: block;
        //margin: 20px 0px 20px 10px;
        margin: 10px 10px;
        padding: 5px 20px;
        line-height: 32px;
        text-align: center;

        &:first-of-type {
            margin-top: 50px;
        }
    }
}

.logo {
    // margin-bottom: 20px;
    // border: 1px solid #000;
    // height: 43px;
}

.logo-text {
    display: inline-block;
    font-family: $logo-font-family;
    text-transform: uppercase;
    line-height: 50px;
    font-size: 35px;
    color: $color-blue;
    //letter-spacing: -3px;
}

.logo-img {
    // border-radius: 50%;
    height: 45px;
    width: 45px;
    margin-top: -13px;
    z-index: 1000;
}

.intro {
    // vertial center align the div
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 80vh;
    //set height setting for different screen size
    @media (max-width: 768px) {
        height: auto;
    }
    @media (max-width: 480px) {
        height: auto;
    }
}

#gradient-canvas {
    display: block;
    position: fixed;

    width: 100vw;
    height: 100vh;
    --gradient-color-1: #273be2;
    --gradient-color-2: #f4c2c2;
    --gradient-color-3: #fe6f5e;
    --gradient-color-4: #f1b42f;
    z-index: -100;

    // --gradient-color-1: #4C4BB0;
    // --gradient-color-2: #B2C3FF;
    // --gradient-color-3: #2012AB;
    // --gradient-color-4: #978DFF;
}

.ani-text {
    font-size: 90px;
    font-weight: 300;
    text-align: left;
    display: block;
    line-height: 100px;
    color: #fff;
    // font-family: "Lato";

    padding-top: 40px;
    // margin-top: 235px;
    margin-left: -6px;

    @include media-query($on-palm) {
        // display: inline-block;
        font-size: 50px;
        line-height: 60px;
        // margin-top: 108px;
        // margin-left: 0;
    }

    // text-shadow:
    // -1px -1px 0 $color-blue,
    // 1px -1px 0 $color-blue,
    // -1px 1px 0 $color-blue,
    // 1px 1px 0 $color-blue;
}

.d-logo {
    display: block;

    padding-top: 10%;

    img {
        height: 250px;
    }

    @include media-query($on-palm) {
        img {
            height: 150px;
        }
    }
}

.d-title {
    color: white;
    font-size: 30px;
    line-height: 40px;
    //    font-family: "Lato";

    &.d-en-title {
        margin-top: 20px;
    }
}

.d-slogan {
    font-size: 30px;
    line-height: 40px;
    margin-top: 20px;
    color: rgb(255, 255, 255);
}

.lab-logos {
    margin-top: 60px;

    img {
        display: block;
        float: left;
        height: 50px;

        margin-right: 70px;
    }

    a {
        display: block;
        font-size: 30px;
        line-height: 45px;
        font-weight: 700;
        color: white;

        &:hover {
            text-decoration: none;
        }
    }

    @include media-query($on-palm) {
        width: 180px;

        margin-right: 20px;
    }
}

#lines-canvas {
    display: block;
    position: fixed;
    background: linear-gradient(to bottom right, #273be2, #fe6f5e, #f1b42f);
    height: 100vh;
    width: 100vw;
    z-index: -100;
    animation: gradient 240s ease infinite;

    svg {
        height: 130vh;
        width: 60vw;
    }

    // path {
    //     fill: none;
    //     stroke: #F4C2C2;
    //     stroke-width: 0.5px;
    //     vector-effect: non-scaling-stroke;
    // }
}

.link_button {
    // display: inline-block;
    padding: 10px 20px;
    margin-top: 40px;
    border: 1px solid #fff;
    font-size: 20px;
    font-weight: 500;
    // text-transform: uppercase;
    letter-spacing: 2px;
    text-align: center;
    transition: all 0.3s;
    color: #fff;
    font-family: $font-cn-family;
    text-decoration: none;
    border-radius: 4px;

    &:visited {
        color: #fff;
    }

    &:hover {
        background: #fff;
        color: $color-red;
        text-decoration: none;

        a {
            color: $color-red;
            text-decoration: none;
        }
    }

    a {
        color: #fff;
    }

    @include media-query($on-palm) {
        &:nth-child(2) {
            margin-top: 20px;
        }
    }
}

#page_info,
#page_apply,
#page_about {
    display: block;
    padding-bottom: 50px;
    min-height: 100vh;

    // position: fixed;

    //min-height: 100vh;
    width: 100vw;

    h1 {
        font-size: 34px;
        color: $color-text-black;
        margin: 50px 0 20px 0;
        font-weight: 300;

        & + h1 {
            margin-top: 5px;
        }
    }

    h2 {
        font-size: 25px;
        margin: 25px 0 0 0;
        font-weight: 500;
        text-transform: uppercase;

        & + h2 {
            margin-top: 5px;
        }

        & + h3 {
            margin-top: 25px;
        }
    }

    h3 {
        font-size: 20px;
        font-weight: 300;
        margin: 10px 0;

        & + h3 {
            margin-top: 5px;
        }
    }

    h1,
    h2,
    h3 {
        & + p,
        & + ul {
            margin: 15px 0 25px;

            font-weight: 300;
        }
    }

    .info-cn .info {
        h2 {
            margin-top: 65px;
        }
    }

    .content-cn {
        h2 {
            margin-top: 90px;
        }
    }
}

.banner-row {
    background: linear-gradient(to bottom right, #273be2, #fe6f5e, #f1b42f);
}

.banner {
    display: block;
    margin-top: 100px;
    margin-left: 20px;

    img {
        height: 180px;
        margin-bottom: 10px;

        @include media-query($on-palm) {
            height: 120px;
        }
    }
}

.site-footer {
    font-size: small;
    background-color: #fe6f5e;
    margin-top: 100px;
}

.copyright_info {
    color: #fff;
}

.topic-info-cn,
.topic-info-en {
    margin-top: 50px;
    font-size: 20px;
    font-weight: 500;
    // color: $color-red !important;

    p {
        margin: 5px 0;

        a {
            color: $color-blue;
        }

        &::lang(zh) {
            margin: 10px 0;
        }
    }

    h1 {
        margin-top: 5px !important;
        // color: $color-red !important;
    }
}

.language-tag {
    display: none;

    @media (max-width: 991.98px) {
        display: block;
        color: $color-red;
        text-align: center;
        margin: 20px 0 40px;
        padding: 10px 20px;
        border: 1px solid $color-red;
        //background-color: $color-red;

        &:visited {
            color: $color-red;
        }
        &:hover {
            background-color: $color-red;
            color: white;
            text-decoration: none;
        }
    }
}

#apply_form {
    min-height: 80vh;
    width: 100%;
}
