// Define defaults for each variable.

$base-font-family: 'Lato', Arial, sans-serif;
$sans-font-family: 'Lato',"Helvetica Neue", Helvetica, Arial, sans-serif;
$logo-font-family: 'Julius Sans One',"Helvetica Neue", Helvetica, Arial, sans-serif;
//$logo-font-family: 'Baloo 2', "Helvetica Neue", Helvetica, Arial, sans-serif;
$base-font-size:   1em;
$base-font-weight: 300;
$small-font-size:  $base-font-size * 0.875;
$base-line-height: 1.5;

$font-cn-family: "Noto Sans SC", sans-serif;

// $title-font-family: 'Cormorant Garamond', serif;

$spacing-unit:     30px !default;

$text-color:       #111 !default;
$background-color: #fdfdfd !default;
$brand-color:      #2a7ae2 !default;

$grey-color:       #828282 !default;
$grey-color-light: lighten($grey-color, 40%) !default;
$grey-color-dark:  #424242 !default;

// Width of the content area
$content-width:    800px !default;

$on-palm:          600px !default;
$on-laptop:        800px !default;

// Use media queries like this:
// @include media-query($on-palm) {
//   .wrapper {
//     padding-right: $spacing-unit / 2;
//     padding-left: $spacing-unit / 2;
//   }
// }
@mixin media-query($device) {
  @media screen and (max-width: $device) {
    @content;
  }
}

// Import partials.
@import
  "minima/base",
  "minima/layout",
  "minima/syntax-highlighting",
  "custom"
;
